.DrawToggle {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: flex;
    padding: 30px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.DrawToggle div {
    width: 90%;
    height: 3px;
    background-color: white;
}

@media (min-width: 680px) {
    .DrawToggle {
        display: none;
    }
}

